* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: none;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background: #232526;
  background: -webkit-linear-gradient(to right, #414345, #232526);
  background: linear-gradient(to right, #414345, #232526);
  color: #fff;
  font-size: 25px;
}

.wrapper {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  width: 70%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  margin: 10px;
  color: #845ec2;
}

.status {
  width: 100%;
  background-color: #fafafa;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 1px 1px 5px 10px rgba(0, 0, 0, 0.3);
}

.status__item {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #43425d;
  padding: 8px;
  border-bottom: 2px solid #f1f1f3;
}

.status__item:last-child {
  border: none;
}

.status__flag {
  background-color: #66c25e;
  padding: 5px;
  border-radius: 20px;
  font-size: 20px;
  font-weight: lighter;
}

.status__flag .error {
  background: rgba(255, 101, 101);
}

.btn-container {
  margin-top: 20px;
}

.statusBtn {
  width: 200px;
  background-color: transparent;
  border: 2px solid #845ec2;
  border-radius: 5px;
  padding: 10px;
  font-size: 20px;
  color: #fff;
  transition: all 0.5s;
  cursor: pointer;
}

.statusBtn:hover {
  background-color: #845ec2;
}

@media (max-width: 768px) {
  .status__item {
    font-size: 20px;
  }

  .status__flag {
    font-size: 15px;
  }
}

@media (max-width: 556px) {
  .container {
    width: 90%;
  }

  h1 {
    font-size: 30px;
  }

  .status__item {
    font-size: 14px;
  }

  .status__flag {
    font-size: 12px;
  }
}
